@import '../../../Variables.scss';
.hotEventCardWrapper {
  width: 100%;
  position: relative;
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 2%;

  .logoWrapper {
    flex: 2;
    > img {
      display: block;
      width: 80%;
    }
  }

  .hotEventContent {
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    flex: 3;
    gap: 15px;
    > h3 {
      font-weight: 900;
      font-size: 18px;
      line-height: 24px;
      align-items: center;
      color: $color-primary;
      margin-bottom: 0;
    }
    > h1 {
      font-weight: 900;
      font-size: 40px;
      line-height: 48px;
      align-items: center;
      margin-bottom: 0;
    }
    .statsWrap {
      position: relative;
      width: 100%;
      //   text-align: left;
      background-color: $color-dark-gray;
      border-radius: 4px;
      padding: 10px;
      display: flex;
      justify-content: space-between;
      align-items: center;
      column-gap: 20px;
      .statsItem {
        flex: 1;
        display: flex;
        // align-items: flex-start;
        justify-content: space-between;
        h2 {
          line-height: 25px;
        }
        .totalStats {
          width: 100%;
          display: flex;
          align-items: center;
          h2 {
            margin: 0;
            font-size: 18px;
            font-weight: 900;
            // line-height: 25px;
          }
          img {
            height: 16px;
            margin-right: 10px;
          }
        }
      }
    }
    > p {
      font-weight: 400;
      font-size: 18px;
      line-height: 30px;
      color: #000000;
      opacity: 0.7;
      margin-bottom: 0;
    }
    .buttonWrapper {
      display: flex;
      justify-content: flex-start;
      align-items: center;
      gap: 5%;
    }
  }
}
.gray {
  background-color: #989898;
  box-shadow: none;
}

@media (max-width: 765px) {
  .hotEventCardWrapper {
    flex-direction: column;
    width: 100%;
    align-items: center;
    margin: 0 auto;

    .logoWrapper {
      margin: 0 auto 10px;
      > img {
        display: block;
        margin: 0 auto;
      }
    }
    .hotEventContent {
      align-items: center;
      > h1 {
        text-align: center;
      }
      > p {
        text-align: justify;
      }
      .statsWrap {
        .statsItem {
          .totalStats {
            justify-content: center;
          }
        }
      }
      .buttonWrapper {
        width: 100%;
        justify-content: space-between;
      }
    }
  }
}
