.daoIntroCard {
  background: #3f9a55;
  box-shadow: 0px 20px 20px rgba(25, 25, 25, 0.1);
  border-radius: 4px;
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 20px 20px 0 20px;
  overflow: hidden;

  > h3 {
    font-weight: 700;
    font-size: 20px;
    line-height: 30px;
    text-align: center;
    letter-spacing: -0.25px;
    color: #ffffff;
  }
  > p {
    text-align: center;
    color: #ffffff;
  }

  @media(max-width: 765px) {
    > h3 {
      font-size: 18px;
      line-height: 25px;
      margin-bottom: 5px;
    }
    > p {
      font-size: 13px;
    }
  }
}
