@import '../../Variables.scss';

.Home {
  position: relative;
  width: 100%;
  overflow-x: hidden;
}

.section {
  position: relative;
  width: 100%;
  background-size: cover;
  background-repeat: no-repeat;
}

.banner {
  min-height: 700px;
  position: relative;
}

.green {
  background-color: $color-primary;
}

.assorted {
  background: linear-gradient(white 50%, $color-primary 50%);
}

.greenWhite {
  background-color: #f5f5f5;
}

.scrollToTop {
  z-index: 2;
  position: fixed;
  left: 20px;
  bottom: 5vh;
  cursor: pointer;
}

.introContainer {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  display: flex;
  //   align-items: center;
  justify-content: center;
  flex-direction: column;
  align-items: center;
  max-width: 100%;
  // padding-top: 100px;
  // position: unset;
  .introText {
    text-align: center;
    width: 70%;
    min-width: 750px;
    h2 {
      font-weight: 900;
      font-size: 70px;
      line-height: 76px;
      color: #ffffff;
    }
    p {
      font-weight: 400;
      font-size: 18px;
      line-height: 33px;
      color: #ffffff;
      opacity: 0.7;
    }

    .linkGroup {
      display: flex;
      margin-top: 50px;
      justify-content: center;
      align-items: center;
      flex-direction: row;
      column-gap: 20px;

      .projectDetailButton {
        background: #fff;
        font-weight: 600;
        font-size: 14px;
        color: #262626;
        box-shadow: none;
        border-radius: 4px;
        padding-left: 30px;
        padding-right: 30px;
        width: 150px;
      }

      .profileButton {
        background-color: #05b3ff;
        box-shadow: 0 4px 10px #05b3ff;
        color: white;
      }
    }
  }
}
.ongoingProjects {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 30px;
  .ongoingIcon {
    display: block;
    margin: 50px auto 0;
  }
  > h2 {
    font-weight: 900;
    font-size: 36px;
    line-height: 48px;
    color: #000000;
    text-shadow: 0px 0px 10px rgba(255, 255, 255, 0.75);
  }
}

.projectsIntroContainer {
  max-width: 1100px;
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: stretch;
  gap: 15px;
  margin-top: -120px;
}
.introImg {
  width: 45%;
}

.target {
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  > img,
  h1 {
    display: block;
  }
  > h1 {
    font-weight: 900;
    font-size: 64px;
    line-height: 80px;
    text-align: center;
    color: #ffffff;
    margin-top: 32px;
  }

  .instructionWrap {
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    gap: 5px;
    a {
      display: block;
      font-weight: 700;
      font-size: 20px;
      margin-right: 20px;
      text-decoration: none;
      color: white;
      img {
        width: 25px;
      }
    }
  }

  .targetText {
    position: relative;
    text-align: left;
    padding: 30px 30px 0 30px;
    display: flex;
    justify-content: space-around;
    align-items: flex-start;
    margin-top: 50px;

    p {
      width: 25%;
      color: white;
      display: flex;
      align-items: center;
      flex-direction: column;
      gap: 5px;
      img {
        width: 50px;
        // margin-right: 20px;
      }
      span {
        font-size: 22px;
        line-height: 28px;
        font-weight: 500;
        text-align: center;
      }
    }
  }
}

.link {
  display: block;
  width: 100%;
  background: #ffdc30;
  border-radius: 4px;
  white-space: nowrap;
  font-size: 22px;
  font-weight: 700;
  line-height: 50px;
  color: #262626;
  text-align: center;
  padding: 5px 0;
  cursor: pointer;
  &:hover {
    color: $color-primary;
  }
}

.hotEvents {
  margin: 50px auto;
}

.projectContainer {
  h2 {
    font-weight: 900;
    font-size: 36px;
    text-align: left;
  }
}

.reasonContainer {
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 30px;
  .reasonText {
    font-weight: 700;
    font-size: 14px;
    line-height: 24px;
    text-align: center;
    color: #000000;
    opacity: 0.7;
  }
  > h3 {
    font-weight: 900;
    font-size: 14px;
    line-height: 24px;
    color: rgba($color: #a7a7a7, $alpha: 1);
    > span {
      text-decoration-line: underline;
      color: #3f9a55;
      opacity: unset;
    }
  }
}

.daoIntroContainer {
  h2 {
    font-weight: 900;
    font-size: 36px;
    line-height: 40px;
  }
  p {
    font-size: 20px;
    line-height: 30px;
    font-weight: 400;
  }
  .daoIntroCardWrap {
    position: relative;
    width: 100%;
    padding: 50px;
    .daoIntroCard {
      position: relative;
      width: 100%;
      padding: 24px;
      background: linear-gradient(225.78deg, #fbfbfb 0%, #ffffff 98.68%);
      box-shadow: 0px 20px 25px -5px rgba(25, 25, 25, 0.16);
      border-radius: 4px;
      img {
        width: 56px;
        margin-bottom: 15px;
      }
      h2 {
        font-size: 20px;
        font-weight: 700;
      }
      p {
        font-size: 16px;
        line-height: 24px;
        font-weight: 400;
        margin: 0;
      }
    }
  }
}

.newsContainer {
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
  gap: 10px;
  > h2 {
    font-weight: 900;
    font-size: 36px;
    line-height: 48px;
    color: #000000;
    // text-shadow: 0px 0px 10px;
  }
}
.faqContainer {
  // background-color: $color-gray;
  .faqHeader {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 10px;
    // background-color: white;
    > h2 {
      font-weight: 800;
      font-size: 36px;
      margin-bottom: 0;
    }
  }

  .customCollapse {
    width: 100%;
    margin: 50px 0;
    border-radius: 8px;
    // border: 1px solid rgba(0, 0, 0, 0.1);
    background-color: white;
    // :global(.ant-collapse-item-active) {
    //   :global(.ant-collapse-content) {
    //     background-color: rgba(63, 154, 85, 0.2);
    //   }
    //   :global(.ant-collapse-header) {
    //     background-color: rgba(63, 154, 85, 0.2);
    //   }
    // }
    .customPanel {
      text-align: left;
      margin-bottom: 5px;
      p {
        span {
          color: rgb(18, 180, 199);
          font-weight: 600;
          cursor: pointer;
        }
      }

      &:first-child {
        border-top-left-radius: 8px;
        border-top-right-radius: 8px;
      }
      &:last-child {
        border-bottom-left-radius: 8px;
        border-bottom-right-radius: 8px;
      }
      background-color: #f5f5f5;
      border: 1px solid rgba(0, 0, 0, 0.1);
      :global(.ant-collapse-header) {
        // background-color: $color-primary;
        font-weight: 700;
        font-size: 20px;
      }
      :global(.ant-collapse-content) {
        background-color: $color-primary;
        font-weight: normal;
        font-size: 15px;
        color: #f0efef;
      }

      a {
        color: rgb(18, 180, 199);
        font-weight: 600;
        cursor: pointer;
      }
    }
  }
}
