@media (max-width: 765px) {
  .introContainer {
    flex-direction: column;
    // padding-top: 100px;

    .introText {
      width: 90%;
      min-width: fit-content;
      h2 {
        font-size: 40px;
        line-height: 46px;
      }
      p {
        line-height: 25px;
        color: #ffffff;
        opacity: 0.7;
      }
      .linkGroup {
        margin-top: 30px;
      }
    }
    .introImg {
      width: 65%;
      margin-top: 30px;
    }
  }
  .projectsIntroContainer {
    width: 95%;
    //bottom: -150px;
    flex-wrap: wrap;

    img {
      width: 50px;
    }
  }
  .target {
    position: relative;
    width: 100%;
    > h1 {
      font-size: 30px;
    }
    .targetText {
      padding: 10px;
      margin-top: 30px;
      flex-wrap: wrap;
      h2 {
        font-size: 24px;
      }
      p {
        width: 50%;
        img {
          width: 25px;
        }
        span {
          font-size: 15px;
          line-height: 18px;
        }
      }
    }
  }
  .link {
    font-size: 20px;
  }

  .projectContainer {
    h2 {
      font-size: 24px;
    }
  }

  .reasonContainer {
    .reasonWrap {
      flex-direction: column;
      width: 100%;
      h2 {
        font-size: 36px;
        line-height: 40px;
      }
      .vl {
        border-left: none;
        border-top: 4px solid white;
        align-self: stretch;
        margin: 30px 0;
      }
      .reasonText {
        p {
          margin-bottom: 10px;
        }
      }
    }
    .reasonFigure {
      position: absolute;
      bottom: 0;
      right: 0;
      width: 30%;
    }
  }

  .daoIntroContainer {
    p {
      font-size: 16px;
      line-height: 18px;
    }
    .daoIntroCardWrap {
      padding: 20px;
    }
  }

  .faqContainer {
    h2 {
      font-weight: 800;
      font-size: 36px;
    }
    .customCollapse {
      .customPanel {
        :global(.ant-collapse-header) {
          font-size: 16px;
          line-height: 22px;
        }
        :global(.ant-collapse-content) {
          font-size: 14px;
          line-height: 20px;
        }
      }
    }
  }
}
