.ChainButton {
  position: relative;
}
.chainIcon {
  img {
    width: 30px;
  }
}
.title {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 2%;

  p {
    margin: 2% 0;
    font-size: 18px;
    font-weight: bold;
  }

  img {
    cursor: pointer;
    width: 12px;
    height: 12px;
  }
}
.walletModalWrapper {
  :global(.ant-modal-content) {
    box-sizing: border-box;
    background: white;
    border: 1px;
    border-radius: 10px;
    padding: 0;
    box-shadow: rgba(100, 100, 111, 0.2) 0px 7px 29px 0px;
    font-family: 'Battambang', sans-serif;
  }
  :global(.ant-modal-body) {
    padding: 15px 20px;
  }
}
@media (max-width: 765px) {
  .chainIcon {
    img {
      width: 20px;
    }
  }
}
